@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: "Open Sans", serif;
  color: #f5f5f5!important;
}

.wrapper {
  height: 100vh;
  background-image: url(/weatherApp/static/media/image.bc154d62.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.main {
  height: 55vh;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
  width: 60%;
  margin: 0 auto;
}

.info {
  height: 55vh;
  background: linear-gradient(45deg, rgba(0,0,0, .3), transparent);
  text-align: center;
}

.form {
  height: 55vh;
  padding-top: 100px;
  padding-left: 50px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  width: 50%;
  padding-bottom: 4px;
  font-weight: lighter;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
  padding-left: 5px;
}

input[type="text"],
input[type="text"]:active,
input[type="text"]:hover,
input[type="text"]:focus {
  color: #f5f5f5;
}

input::-webkit-input-placeholder {
  color: #f5f5f5;
}

input::-moz-placeholder {
  color: #f5f5f5;
}

button {
  border: 2px solid rgba(0,0,0, .2);
  font-weight: 700;
  box-shadow: 0px 2px 80px -20px rgba(0, 0, 0, 0.5);
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  border: none;
  cursor: pointer;
  background: linear-gradient(198deg, rgba(0, 0, 0, .4), transparent);
  outline: none!important;
  color: seashell;
}


.wrapper,
.info  {
  display: flex;
  align-items: center;
  justify-content: center;
}

.infoWeath {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0, .3)), to(transparent));
  background: linear-gradient(180deg, rgba(0,0,0, .3), transparent 100%);
}

.infoWeath p {
  padding: 10px;
  margin-bottom: 0;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
}

.infoWeath p span {
  color: bisque;
}

input[type="text"],
.infoWeath p {
  border-bottom: solid 2px rgba(224, 224, 224, 0.5);
}


input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid #e0e0e0;
  -webkit-text-fill-color: #f5f5f5;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

.alert {
    font-size: 18px;
}

.alert span {
    margin-left: 10px;
}

.alert{
    background: rgba(233, 30, 99, 0.3);
    border-radius: 35px;
    margin-top: 33px;
}

.alert.forget_password_error {
    background: rgba(244, 67, 54, 0.8);
    color: #212121;
    border-color: #212121;
    box-shadow: 0 0 10px 1px #212121;
}

.alert.strong {
    text-transform: uppercase;
}

.alert-dismissible .close {
    background: transparent;
    padding: 0;
    top: 10px!important;
    right: -24px;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
}

.alert-dismissible .close:hover {
    color: white;
}






